.cartList {
    display: flex;
    flex-direction: column;
    gap: 50px;
    max-width: var(--innerContainer_main);
    width: 100%;
    padding: 10px 8px;
    margin: 0;
  }
  
  @media (max-width: 1267px) {
    .cartList {
      align-items: center;
    }
  }
  
  .emptyCart {
    font-size: var(--fz-m);
  }
  
  .link {
    color: var(--color_6);
    font-weight: 700;
    font-size: var(--fz-m_1);
    text-decoration: underline;
  }
  